import React, { Component } from 'react';
import { Route, Link, Switch } from 'react-router-dom';

// import Typography from 'material-ui/Typography';
import Grid from 'material-ui/Grid';

import injectSheet from 'react-jss';

import UploadScreen from './screens/UploadScreen';
import { HeroMap } from './components/HeroMap';
import { Header } from './components/Header';

const dummyModelList = [
  {
    name: '  ',
    description: `1966 4 4   .        .             .         .                .               .   1966 7         , 8     .[4]        ,            ,       9 8  .     '   '        ()  .[5]       .[6]

    , 1966 11 26      . ( 4 125)~(  93)  (), ( 4 125)~(  85)  ()  .[7]    1984 11 7 .`
  },
  { name: ' ' },
  { name: ' ' },
  { name: '2013  ' },
  { name: '   ' },
  { name: '  ' },
  { name: '  ' },
]


const styles = {
  App: {
    'max-width': '1020px',
    'margin': '0 auto',
  },
  main: {
    width: '100%',
    height: '40vh',
    position: 'relative',
  },
  overlaidTitle: {
    position: 'absolute',
    bottom: '10px',
    right: '10px',
    'background-color': '#8a8a8a',
    color: 'white',
    padding: '8px',
  },
  body: {

  }
};

class App extends Component {
  render() {
    const { classes } = this.props;

    const selectedModelIndex = 0;

    return (
      <div className={classes.App}>
        <Header />

        <div className={classes.main}>
          <HeroMap />
          <h1 className={classes.overlaidTitle}>  </h1>
        </div>

        <Grid container>

          <Grid item xs={12} md={3}>
            <ul>
              {dummyModelList.map((model, idx) => (
                idx === selectedModelIndex
                ? <li>{model.name}</li>
                : <li>{model.name}</li>
              ))}
            </ul>
          </Grid>
          <Grid item xs={12} md={9}>
            <p>
              { dummyModelList[selectedModelIndex].description }
            </p>
            <div>Map tiles by Stamen Design, under CC BY 3.0. Data by OpenStreetMap, under CC BY SA.</div>
          </Grid>
        </Grid>

      </div>

    );
  }
}

export default injectSheet(styles)(App);



